//流量排行
<template>
  <div>
      <crumbs menu='商场流量' subMenu='流量排行' /> 
      <div class="container sql">
        <el-row type="flex" justify="space-around">
          <el-col :span='1'>
            <span class="text fontCenter" >商场</span>
          </el-col>
          <el-col :span='2'>
            <market @marketGet='marketGet' :marketSet='marketName'/>
          </el-col>
          <el-col :span='1'>
                    <span class="text fontCenter" >指标</span>
                  </el-col>
          <el-col :span='5'>
            <el-col :span='12'>
              <el-select v-model="indexTags.value" multiple placeholder="请选择" collapse-tags @change="multipleChange">
                      <el-option
                        v-for="item in indexTags.options"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                      </el-option>
              </el-select>
            </el-col>
            <el-col :span='12'>
              <el-tag
                      @close="handleClose1(tag.name)"
                      v-for="tag in tagGet"
                      :key="tag.name"
                      closable
                      :type="tag.type">
                      {{tag.name}}
                    </el-tag>
            </el-col>
          </el-col>
          <el-col :span='1'>
                <span class="text fontCenter" >区域</span>
          </el-col>
          <el-col :span='5'>
            <el-col :span='12'>
              <el-select v-model="areaTags.value" multiple placeholder="请选择" collapse-tags @change="multipleChange">
                      <el-option
                        v-for="item in areaTags.options"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                      </el-option>
              </el-select>
            </el-col>
            <el-col :span='12'>
              <el-tag
                      @close="handleClose2(tag.name)"
                      v-for="tag in tagGet2"
                      :key="tag.name"
                      closable
                      :type="tag.type">
                      {{tag.name}}
                    </el-tag>
            </el-col>
          </el-col>
          <el-col :span='1'>
                    <span class="text fontCenter" >排行</span>
                  </el-col>
          <el-col :span='4'>
            <div>
                      <el-select v-model="rankValue" placeholder="请选择" filterable :style="widthSet" :disabled='rankDis' >
                        <el-option
                          v-for="item in rankList"
                          :key="item.value"
                          :label="item.label"
                          :value="item.value">
                        </el-option>
                      </el-select>
            </div>
          </el-col>
          <el-col :span='4'>
              <el-button type="primary" icon="el-icon-search" @click='onQuery'>查询</el-button>
              <el-button type="primary" icon="el-icon-refresh-right" @click="reSet">重置</el-button>

          </el-col>
        </el-row>
      </div>
      <el-card class="marginTop10" shadow="hover">
        <template #header>
              <div class="card-header">
                <el-row>
                  <el-col :span='4'>
                    <span class="text" >流量排行</span>
                  </el-col>
                  <el-col :span='7'>
                    <time-select @timeGet='timeGet'></time-select>
                  </el-col>
                  <el-col :span='9'>
                    <el-date-picker
                        :disabled='datePickerAble'
                        v-model="timeRange"
                        type="daterange"
                        :shortcuts="shortcuts"
                        range-separator="至"
                        start-placeholder="开始日期"
                        end-placeholder="结束日期"
                        align="right">
                      </el-date-picker>
                  </el-col>
                  <el-col :span='4'>
                    <export-excel file-name="流量排行" :header="tableList.map(item=>item.label)"
                      :filter-filed="tableList.map(item=>item.prop)" :data="tableData">导出</export-excel>
       
                  </el-col>
                </el-row>
              </div>
            </template>
            <el-row>
                      <el-table class="font-set"
                        :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)"
                        style="width: 100%;margin-bottom: 10px;margin-top:10px;font-size: 9px"
                        row-key="tableData"
                        key="1"
                        border
                        @sort-change="sort_change"
                        :current-page="currentPage"
                      
                        stripe
                      >
                      <el-table-column
                        v-for="col in tableList"
                        :key="col.id"
                        :prop="col.prop"
                        :label="col.label"
                        :align="col.align"
                        sortable>
                      </el-table-column>
                      <el-table-column
                        prop="area"
                        label="区域"
                        sortable
                        align="center"
                        >
                      </el-table-column>
                    </el-table>
            </el-row>
              <div class="pagination flexCenter" >
                  <el-pagination 
                      class="flexCenter"
                      @size-change="handleSizeChange"
                      @current-change="handleCurrentChange"
                      :page-sizes="[10]"
                      :page-size="pagesize"
                      layout="total, sizes, prev, pager, next, jumper"
                      :total="total">     
                  </el-pagination>
              </div>
      </el-card>
  </div>
</template>
<script>
import Crumbs from '../pages/Crumbs'
// import IndexSelect from '../pages/indexSelect/IndexSelect'
// import AreaSelect from '../pages/indexSelect/AreaSelect'
import TimeSelect from '../pages/TimeSelectNoText'
import axios from 'axios'
import ExportExcel from '../pages/excel/ExportExcel'
import MarketSelect from '../pages/indexSelect/MarketSelectNotext'

export default {
  data(){
    return {
        marketName:'',
        textWidth:'',
        areaName:'全部',//选择的区域
        datePickerAble:true,
        time:'',//选择的时间
        timeRange: '',//选择的时间范围
        shortcuts: [{
                text: '最近一周',
                value: (() => {
                  const end = new Date();
                  const start = new Date();
                  start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                  return [this.$moment().subtract(7, 'days'), end]
                })()
              }, {
                text: '最近一月',
                value: (() => {
                  const end = new Date();
                  return [this.$moment().subtract(1, 'months'), end]
                })()
              }, {
                text: '最近一年',
                value: (() => {
                  const end = new Date();
                  const start = new Date();
                  start.setFullYear(start.getFullYear()-1)
                  // start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                  return [this.$moment().subtract(1, 'years'), end]
                })()
              }],
        rankValue:'全部',
        rankDis:false,
        rankList:[{
          value:'全部',
          label:'全部'
        },
        {
          value:'前10位',
          label:'前10位'
        },{
          value:'前50位',
          label:'前50位'
        },
        ],
        tableData:[],
        currentPage:1,//当前页
        pagesize:10,//每页数量
        total:0,//
        indexName:'',//选择的指标
        index:[{
            value: '客流量',
            label: '客流量',
                }, {
            value: '集客力',
            label: '集客力',
                }, {
            value: '提袋率',
            label: '提袋率',
                }, {
            value: '游逛深度',
            label: '游逛深度',
                }],
        indexTags:{
          value:['客流量'],
          options:[{
            value: '客流量',
            label: '客流量',
                }, {
            value: '集客力',
            label: '集客力',
                }, {
            value: '提袋率',
            label: '提袋率',
                }, {
            value: '游逛深度',
            label: '游逛深度',
                }]
        },
        areaTags:{
          value:['楼层'],
          options:[ {
            value: '楼层',
            label: '楼层',
                }, {
            value: '通道',
            label: '通道',
                }, {
            value: '楼栋',
            label: '楼栋',
                },{
            value: '店铺',
            label: '店铺',
                }]
        },
        tableList:[]

    }
  },
  methods:{
    marketGet(val){
      this.marketName=val
      },
    areaGet(val){
        this.areaName=val
      },
    timeGet(val){
        console.log(val)
        this.time=val
        if(val=='自定义时间'){
          this.datePickerAble=false
        }else this.datePickerAble=true
      },
    indexGet(val){
        this.indexName=val
      },
    handleSizeChange(val) {
            this.pagesize=val;
      },
    handleCurrentChange(val) {
            this.currentPage = val;
      },
    sort_change(params){  
        console.log(params)
        if(params.order=="ascending"){
          this.tableData.sort((a,b)=>a[params.prop]-b[params.prop])
        }else if(params.order=="descending"){
          this.tableData.sort((a,b)=>b[params.prop]-a[params.prop])
        }else {
          this.tableData.sort((a,b)=>a.flowRank-b.flowRank)
        }
        
      },
    reSet(){

        this.indexName='客流量'
        this.rankValue='全部',
        this.marketName='万达'
      },
    timeIntval(startTime,endTime){
          let arr = []
          let moment=this.$moment
          startTime = moment(startTime).format('YYYY-MM-DD')
          endTime = moment(endTime).format('YYYY-MM-DD')
          while (moment(startTime).isBefore(endTime)) {
            arr.push(moment(startTime).format('MM-DD'))
            startTime = moment(startTime).add(1, 'days').format('YYYY-MM-DD')
          }
          arr.push(endTime)
          return arr
      },
      
    handleClose1(tag) {
     this.indexTags.value=this.indexTags.value.splice(this.indexTags.value.indexOf(tag), 1);
    },
    handleClose2(tag) {
        this.areaTags.value.splice(this.areaTags.value.indexOf(tag), 1);
    },
    onQuery(){
       axios('https://mock.mengxuegu.com/mock/6076fee5990ff82a18f95b5f/item/getTrafficRanking').then(res=>{
      this.tableList=[]
      console.log(this.indexTags.value)
      let sortList=res.data.list.filter(item=>this.areaTags.value.indexOf(item.area)!=-1)
      let newArr=sortList
      let first=''
      for(let i=0;i<this.indexTags.value.length;i++){
            let indexProps=''
            if(this.indexTags.value[i]=='客流量'){
              indexProps='flow'
            }else if(this.indexTags.value[i]=='集客力'){
              indexProps='getPower'
            }else if(this.indexTags.value[i]=='提袋率'){
              indexProps='rate'
            }else if(this.indexTags.value[i]=='游逛深度'){
              indexProps='deep'
            }
          if(i==0){
            first=indexProps
          }
          console.log(indexProps)
          newArr=newArr.sort((a,b)=>{return b[indexProps]-a[indexProps]})
          console.log(newArr)
          let newStr="rank"+indexProps
          for(let i=0;i<newArr.length;i++){
            newArr[i][newStr]=i+1
          }
            this.tableList.push({
              prop:newStr,
              label:this.indexTags.value[i]+'排名',
              align:"center",
              id:i+'_'
            })
            console.log(indexProps)
            if(indexProps=="rate"){
              console.log(indexProps)
              console.log(
                {
              prop:indexProps,
              label:this.indexTags.value[i]+'(%)',
              align:'center',
              id:i
            }
              )

              this.tableList.push({
              prop:indexProps,
              label:this.indexTags.value[i]+'(%)',
              align:'center',
              id:i,
            })
            }else{
              this.tableList.push({
              prop:indexProps,
              label:this.indexTags.value[i],
              align:'center',
              id:i
            })
            }
            newArr=newArr.sort((a,b)=>{return b[first]-a[first]})
          }

      console.log(newArr)
      console.log(this.tableList)
      this.tableData=newArr
      this.total=this.tableData.length
      })
    
    },
    multipleChange(){
      console.log(this.indexTags.value)
      if(this.indexTags.value.length>1){
        this.rankDis=true
      }else{
        this.rankDis=false
      }
    }
  },
  components:{
    crumbs:Crumbs,
    timeSelect:TimeSelect,
    ExportExcel,
    market:MarketSelect,

    // areaSelect:AreaSelect,
    // indexSelect:IndexSelect,
  },
  mounted(){
    this.timeRange=[this.$moment().subtract(7, 'days').format(),this.$moment()]
    axios('https://mock.mengxuegu.com/mock/6076fee5990ff82a18f95b5f/item/getTrafficRanking').then(res=>{
      console.log(res.data.list)
      let sortList=res.data.list.filter(item=>this.areaTags.value.indexOf(item.area)!=-1)
      let newArr=[]
      for(let i=0;i<this.indexTags.value.length;i++){
            let indexProps=''
            if(this.indexTags.value[i]=='客流量'){
              indexProps='flow'
            }else if(this.indexTags.value[i]=='集客力'){
              indexProps='getPower'
            }else if(this.indexTags.value[i]=='提袋率'){
              indexProps='rate'
            }else if(this.indexTags.value[i]=='游逛深度'){
              indexProps='deep'
            }
          sortList=sortList.sort((a,b)=>{return b[indexProps]-a[indexProps]})
          let newStr="rank"+indexProps
          for(let i=0;i<sortList.length;i++){
            newArr.push({
              ...sortList[i],
              [newStr]:i+1
            })
          }
            this.tableList.push({
              prop:newStr,
              label:this.indexTags.value[i]+'排名',
              align:"center",
              id:i+'_'
            })
            this.tableList.push({
              prop:indexProps,
              label:this.indexTags.value[i],
              align:'center',
              id:i
            })
          }
      console.log(newArr)
      this.tableData=newArr
      this.total=this.tableData.length
      })
    

  },
  computed:{
    widthSet(){
      return `width:calc(90% - ${this.textWidth}px;`
    },
    tagGet(){
      return this.indexTags.value.map(item=>({name:item,type:''}))
    },
    tagGet2(){
      // console.log(this.indexTags.value.map(item=>({name:item,type:''})))
      return this.areaTags.value.map(item=>({name:item,type:''}))
    }
  },
  watch:{
    time(val){
      if(val==='近7日'){
        this.timeRange=[this.$moment().subtract(7, 'days'),this.$moment()]
      }
      if(val==="近30日"){
        this.timeRange=[this.$moment().subtract(1, 'months'),this.$moment()]
      }
      if(val==="近1年"){
        this.timeRange=[this.$moment().subtract(1, 'year'),this.$moment()]
      }
    },
    
    indexTags(val){
      console.log(val)
      if(val.length>1){
       this.rankDis=true
      }else{
       this.rankDis=false

      }
    }
  }
}
</script>
<style scoped>
.text {
    font-size: 20px;
    font-weight: bolder;
    text-align: center;
    line-height: 40px;
  }
  .flexCenter{
    display: flex;
    align-items: center; /*垂直方向居中*/
    justify-content: flex-end;  /*水平方向居中*/

  }
</style>